			},
			modalOpen: function(item) {
				this.modalData = item;
				this.visible = true;
				document.body.style.overflow = 'hidden';
			},
			modalClose() {
				this.visible = false;
				document.body.style.overflow = '';
			},
			more(){
		        this.isActive = !this.isActive;
				console.log("click");
			}
		},
	}
</script>

